<script>
export default {
  name: "details-component",
  data() {
    return {
      data: {},
    };
  },
  computed: {},
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    async getUrgentDetail(prjNo) {
      try {
        const res = await this.$store.dispatch("getUrgentDetail", prjNo);
        const data = res.data.data;
        this.data = data;
      } catch (err) {
        console.log(err);
      }
    },
  },
  mounted() {
    const prjNo = this.$route.params.prjNo;
    this.getUrgentDetail(prjNo);
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>
<template src="./details.html"></template>
<style lang="sass" scoped src="../../../../public/styles/custom.sass"></style>
